import React from "react";
import { Link } from "react-router-dom";
import "./AppHeader.css";
import logo from "../../img/woodec-logo.svg";
import logoTablet from "../../img/logoMobile.svg";
import iconMobileMenu from "../../img/menuIcon.svg";
import IconLang from "../../img/arrow-down-3101.svg";

const AppHeader = (props) => {
  const lang = props.lang;
  const data = props.data;
  const setLang = props.setLang;
  const langs = props.langs;
  const setActiveMenuItem = props.setActiveMenuItem;
  const activeMenuItem = props.activeMenuItem;
  const setExpandedAccordion = props.setExpandedAccordion;
  //const [langDrop, setLangDrop] = useState(false);
  //const [mobileMenu, setMobileMenu] = useState("");
  //const [anchorElNav, setAnchorElNav] = React.useState(null);
  //const [anchorElUser, setAnchorElUser] = React.useState(null);
  const handleChange = (e) => {
    console.log(e.target.value);
    setLang(e.target.value);
  };

  // const handleOpenNavMenu = (event) => {
  //   setAnchorElNav(event.currentTarget);
  // };
  // const handleOpenUserMenu = (event) => {
  //   setAnchorElUser(event.currentTarget);
  // };
  //
  // const handleCloseNavMenu = () => {
  //   setAnchorElNav(null);
  // };

  //const handleCloseUserMenu = () => {
  //  setAnchorElUser(null);
  //};

  return (
    <>
      <header className="containerNavbar">
        <div className="containerLogo">
          <Link to={`/`} onClick={() => setActiveMenuItem("/")}>
            <img src={logo} className="logo" alt="WOODEC" />
          </Link>
        </div>
        <div className="spacer"></div>
        <nav className="containerMenu">
          {Object.keys(data).map((key) => {
            return (
              <Link
                to={`/${key}`}
                onClick={() => {
                  setActiveMenuItem(`${key}`);
                  setExpandedAccordion("history");
                }}
                key={`menu-${key}`}
              >
                <li
                  className={`nav-link ${
                    key === activeMenuItem ? "active" : ""
                  }`}
                >
                  {data[key]}
                </li>
              </Link>
            );
          })}
        </nav>
        <div className="spacer"></div>
        <nav className="dropdown">
          <button className="dropbtn">
            {lang}
            <img src={IconLang} className="iconLang" alt="" />
          </button>

          <div className="dropdown-content">
            {langs.map((langMenu) => {
              if (langMenu !== lang) {
                return (
                  <button
                    onClick={handleChange}
                    value={langMenu}
                    key={`lang-${langMenu}`}
                  >
                    {langMenu}
                  </button>
                );
              } else {
                return "";
              }
            })}
          </div>
        </nav>
      </header>
      <header className="containerNavbarTablet">
        <div className="containerLogoTablet">
          <Link to={`/`} onClick={() => setActiveMenuItem("/")}>
            <img className="logoTablet" src={logoTablet} alt="" />
          </Link>
        </div>
        <div className="spacerTablet"></div>
        <div className="dropdown">
          <button className="dropbtn">
            {lang}
            <img src={IconLang} className="iconLang" alt="" />
          </button>

          <div className="dropdown-content">
            {langs.map((langMenu) => {
              if (langMenu !== lang) {
                return (
                  <button
                    onClick={handleChange}
                    value={langMenu}
                    key={`lang-${langMenu}`}
                  >
                    {langMenu}
                  </button>
                );
              } else {
                return "";
              }
            })}
          </div>
        </div>
        <div className="menuMobileContainer dropdownMenu">
          <button className="menuButtonMobile dropbtn">
            <img className="menuIconMobile" src={iconMobileMenu} />
          </button>
          <div className="dropdown-contentMenu">
            {Object.keys(data).map((key) => {
              return (
                <Link
                  to={`/${key}`}
                  onClick={() => {
                    setActiveMenuItem(`${key}`);
                    setExpandedAccordion("history");
                  }}
                  key={`menu-${key}`}
                >
                  <button
                    className={`nav-link ${
                      key === activeMenuItem ? "active" : ""
                    }`}
                  >
                    {data[key]}
                  </button>
                </Link>
              );
            })}
          </div>
        </div>
      </header>
    </>
  );
};
export default AppHeader;
